html {
  font-family: 'Montserrat', sans-serif;
  min-height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  /* font-family: sans-serif; */
  background-image: linear-gradient(-225deg, #FCDF8A 0%, #D41872 52%, #FF0066 100%);
  /* background-image: linear-gradient(-225deg, #A445B2 0%, #D41872 52%, #FF0066 100%); */
  min-height: 100%;
  padding-bottom: 5rem;
}
h1, h3  {
  color: white;
  padding: 3rem 0;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  /* font-family: 'Staatliches', cursive; */
  letter-spacing: 1px;
    text-shadow: 2px 2px 3px rgb(165, 165, 165);
}
.lista-citas .media {
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 3rem;
}

.lista-citas .media:last-of-type{
  border-bottom: 0;
}
.lista-citas .media-body p {
  margin:0;
}
.lista-citas .media-body button {
  margin-top: 1rem!important;
}
.lista-citas .media-body span {
  font-weight: bold;
}

label {
  color: white;
}

input[type="date"],
input[type="time"] {
    height: 38px;
    padding: 6px 10px;
    background-color: #fff;
    border: 1px solid #D1D1D1;
    border-radius: 4px;
    box-shadow: none;
    box-sizing: border-box;
}

.cita {
  padding: 2rem;
  background: white;
  border-bottom: 1px solid #e1e1e1;
  color: black;
}

.cita p {
  font-weight: bold;
  margin-bottom: .8rem;
}
.cita p span {
  font-weight: normal;

}
.cita:first-of-type {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.cita:last-of-type {
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.button.eliminar,
.button.eliminar:hover {
  background-color: #c10059;
  color: white;
  margin-top: 2rem;
}

.alerta-error {
  font-family: 'Montserrat', sans-serif;
  background-color: white;
  /* color: rgba(251, 49, 49, 0.74); */
  color: #e42983;
  padding: 1rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  text-align: center;
    text-shadow: 1px 1px 2px rgb(165, 165, 165);
} 

footer{
  padding-top: 3rem;
  align-self: flex-end;
  line-height: 3;
  text-align: center;
  width: 100%;
  font-family: 'Montserrat', sans-serif;
}